@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);


* {
	margin: 0;
	padding: 0;
	font-family: "Roboto", sans-serif;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

.flowersListLinkWrapper {
	position: relative;
	bottom: 75px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.flowersFormLinkWrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}


.flowerCard {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 20px;
	gap: 20px;
}

.flowerToCheck {

}

.flowerChecked {
	text-decoration: line-through;
}
.flowerName {

}

.flowerPrice {

}

.checkFlowerBtn {
	cursor: pointer;
	border: none;
	background-color: #d4d4d4;
}

.checkFlowerBtn:hover {
	background-color: #ececec;
}

.checkFlowerBtn:active {
	background-color: #d4d4d4;
}
.flowersListWrapper {
	margin: 0 auto;
	max-width: 500px;
	border-radius: 10px;
	padding: 20px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	background-color: #ececec;
}

.flowersList {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	grid-gap: 10px;
	gap: 10px;
}

.flowersListTitle {
	font-weight: 700;
}

.flowersElement {
	padding: 10px;
	border: 2px solid transparent;
	border-radius: 10px;
	transition: border 300ms;
}

.flowersUncheckedElement {
	background-color: #cecece;
}

.flowersCheckedElement {
	background-color: #92ff7b;
}

.flowersCheckedElement:hover {
	border: 2px solid black;
}

.flowersUncheckedElement:hover {
	border: 2px solid black;
}
.flowersForm {
	margin: 100px auto 0;
	padding: 30px;
	background-image: url(/flowers-tt%60/static/media/background.d5a9a8a8.jpg);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	border-radius: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	grid-gap: 40px;
	gap: 40px;
	height: 300px;
	max-width: 500px;
}

.flowersInputWrapper {
	position: relative;
	width: 70%;
}

.flowersInput {
	padding: 5px;
	width: 100%;
	font-size: 16px;
	border: transparent;
	background-color: transparent;
	border-bottom: 1px solid ;
	outline: none;
}

.flowersInput::-webkit-input-placeholder {
	color: #898989;
}

.flowersInput:-ms-input-placeholder {
	color: #898989;
}

.flowersInput::placeholder {
	color: #898989;
}

.flowersInput:focus::-webkit-input-placeholder {
	color: #000;
}

.flowersInput:focus:-ms-input-placeholder {
	color: #000;
}

.flowersInput:focus::placeholder {
	color: #000;
}

.inputErr {
	position: absolute;
	bottom: 30px;
	font-weight: 300;
	font-size: 14px;
	color: #ff3333;
}

.addFlowerBtn {
	cursor: pointer;
	width: 50%;
	padding: 15px;
	background-color: #66c44e;
	border: none;
	transition: background-color 300ms;
}

.addFlowerBtn:hover {
	background-color: #5bec3d;
}

.addFlowerBtn:active {
	background-color: #66c44e;
}
